.navbarItems {
    padding: 0px 16px;
    height: 70px;
    background-color: #313434;
    display: flex;
    justify-content: center;
    align-items: center;

    -webkit-box-shadow: 0px 4px 5px 0px rgba(0,0,0,0.6);
    -moz-box-shadow: 0px 4px 5px 0px rgba(0,0,0,0.6);
    box-shadow: 0px 4px 5px 0px rgba(0,0,0,0.6);
}

.navbar__logo {
    color: #ffffff;
    justify-self: start;
    margin-left: 12px;
    font-size: 26px;
    font-family: 'Lobster', cursive;
    font-weight: 500;
    cursor: pointer;
}

.nav__menu {
    margin: 0%;
    padding: 0%;
    object-fit: cover;

    width: 70vw;
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    justify-content: end;
}

.nav__links {
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    color: #e2e2e2;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav__links:hover {
    color: #ffffff;
    background-color: #373a3a;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.nav__links--mobile {
    display: none;
}

.fas {
    height: 28px;
    width: 28px;
    margin-left: 12px;
    color: #ffffff;
}

.menu__icon {
    display: none;
}

@media only screen and (max-width: 960px) {

    .navbarItems {
        position: relative;
        // align-items: center;
        // margin: 0;
        background-color: #313434;
    }

    .nav__menu {
        margin: 0%;
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top: 70px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav__menu.active {
        margin: 0%;
        background: #373a3a;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav__links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        // display: table;
        width: 80%;
        margin: auto;
        display: block;
    }

    .nav__links:hover {
        background-color: #454949;
        border-radius: 0;
    }

    .nav__links--mobile {
        width: 80%;
        margin: 2rem auto;
        padding: 1.5rem;
        display: block;
        text-align: center;
        border-radius: 4px;
        text-decoration: none;
        color: #ffffff;
        font-size: 1.5rem;
    }

    .nav__links--mobile:hover {
        background: #ffffff;
        color: #6668f4;
        transition: 250ms;
    }

    .navbar__logo {
        font-size: 19px;
        margin: 12px;
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 50%);
    }

    .menu__icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }
}