.tarjetas {
    // border: 2px solid red;

    box-sizing: border-box;
    /* fallback for old browsers */
    background: #2b5876;  
    /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(to right, #4e4376, #2b5876);  
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: linear-gradient(to right, #4e4376, #2b5876); 

    // background-color: rgb(31, 31, 58);

    // padding-top: 240px;
    // padding-bottom: 240px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    // background: green;
}

.tarjetas__h2 {
    width: 100%;
    padding-bottom: 24px;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 28px;
    font-weight: 600;
    color: #f2f2f2;
}

.tarjeta {
    margin: 18px 8px;
    border: 5px solid rgb(35, 35, 40);
    border-radius: 14px;
    width: 320px;
    overflow: hidden;
    background: rgb(35, 35, 40);
}

.tarjeta__img {
    position: relative;
    clip-path: polygon(0 0,100% 0,100% 95%, 0 100%);
    object-fit: cover;
}

.tarjeta__img img {
    display: block;
    width: 320px;
}

.detalles {
    margin-top: 6px;
    margin-bottom: 14px;
    padding: 6px 8px;
    color: #f2f2f2;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;

    display: flex;
    flex-direction: column;
    text-align: center;
}

.detalles p {
    margin: 16px 0px;
}

.detalles a {
    margin: auto 60px;
    padding: 5px 16px;
    border: 1px solid #dadada;
    border-radius: 12px;
    font-size: 18px;
    font-weight: 400;
    color: #dadada;
    text-decoration: none;

    /* fallback for old browsers */
    background: #c31432;  
    /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(to right, rgba(195, 20, 50, 0.8), rgba(68, 16, 16, 0.8));  
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: linear-gradient(to right, rgba(195, 20, 50, 0.8), rgba(68, 16, 16, 0.8));
}

.detalles a:hover {
    color: #FFFFFF;
    border: 1px solid #ffffff;

    /* fallback for old browsers */
    background: #c31432;  
    /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(to right, rgba(195, 20, 50, 1), rgba(68, 16, 16, 1));  
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: linear-gradient(to right, rgba(195, 20, 50, 1), rgba(68, 16, 16, 1)); 
}


.wave {
    position: relative;
    bottom: 0px;
    width: 100%;
    height: 160px; 
    overflow: hidden;
}
.wave__svg {
    height: 100%; 
    width: 100%;
}
.wave__path {
    stroke: none; 
    fill: #393e46;
}

.wave3 {
    margin-bottom: 60px;
    position: relative;
    top: 0px;
    width: 100%;
    height: 160px; 
    overflow: hidden;
}
.wave__svg3 {
    height: 100%; 
    width: 100%;
}
.wave__path3 {
    stroke: none; 
    fill: #e2e2e2;
}