.footer {
    // border: 2px solid yellow;
    box-sizing: border-box;

    height: 96px;
    margin: none;
    padding: none;
    background: #212429; // #222831;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer__div {
    box-sizing: border-box;
    padding-bottom: 12px;
    max-width: 1200px;
    height: inherit;
    display: inherit;
    flex-direction: inherit;
    align-items: inherit;
}

.footer__div--p {
    margin: 0;
    color: honeydew;
    // font-family: 'Open Sans', sans-serif;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    font-weight: 500;
    text-align-last: center;
}